import React from "react";
import Navbar from "../Navbar/defaultNav/Navbar";

import "./Header.css";
import Hero from "./Hero/Hero";
const Header = () => {
  return (
    <div className="header">
      <div className="container-fluid header-content">
        <div className="data">
          <Navbar />
          <Hero />
        </div>
      </div>
    </div>
  );
};
export default Header;
