import React from "react";
import one from "./SVGs/1.svg";
import two from "./SVGs/2.svg";
import three from "./SVGs/3.svg";
import four from "./SVGs/4.svg";
const Works = () => {
  return (
    <div className="layout container h-50 text-center mt-5 mb-5">
      <div className="card col-2 ">
        <h3 className="text-right">مجالات وقطاعات الاعمال</h3>
        <button>
          <span> اكتشف المزيد</span>
          <i className="fa-solid fa-arrow-left-long"></i>
        </button>
      </div>
      <div className="card col-2">
        <div className="card-img">
          <img src={one} alt="" />
        </div>
        <div className="card-text">
          <p>عمالة منزلية بالساعة</p>
        </div>
        <button>
          <i className="fa-solid fa-arrow-left-long"></i>
        </button>
      </div>
      <div className="card col-2">
        <div className="card-img">
          <img src={two} alt="" />
        </div>
        <div className="card-text">
          <p>عمالة منزلية بالتأجير الشهري</p>
        </div>
        <button>
          <i className="fa-solid fa-arrow-left-long"></i>
        </button>
      </div>
      <div className="card col-2">
        <div className="card-img">
          <img className="big" src={three} alt="" />
        </div>
        <div className="card-text">
          <p>قطاع الأعمال</p>
        </div>
        <button>
          <i className="fa-solid fa-arrow-left-long"></i>
        </button>
      </div>
      <div className="card col-2">
        <div className="card-img">
          <img className="big" src={four} alt="" />
        </div>
        <div className="card-text">
          <p>القطاع الطبي</p>
        </div>
        <button>
          <i className="fa-solid fa-arrow-left-long"></i>
        </button>
      </div>
    </div>
  );
};
export default Works;
