import React from "react";
import { NavLink } from "react-router-dom";
import "./Navbar.css";

import logo from "./logo.svg";
const Navbar = () => {
  return (
    <div className="navbar dark-nav navbar-expand-lg navbar-light bg-light">
      <div className="container nav-container d-flex wrap">
        <NavLink className="nav-brand" to="">
          <img src={logo} alt="" />
        </NavLink>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarContent"
          aria-controls="navbarContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarContent">
          <div className="w-50">
            <div className=" w-100 logo d-flex">
              <div className="menu">
                <ul className="navbar-nav">
                  <li className="nav-item">
                    <NavLink className="nav-link" to="/efadh-site">
                      الصفحة الرئيسية
                    </NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink className="nav-link" to="">
                      عن الشركة
                    </NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink className="nav-link" to="/efadh-site/services">
                      خدماتنا
                    </NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink className="nav-link" to="">
                      توظيف
                    </NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink className="nav-link" to="">
                      عروض
                    </NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink className="nav-link" to="">
                      اتصل بنا
                    </NavLink>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <div className="more">
            <div>
              <span>
                EN <i className="fa-solid fa-globe"></i>
              </span>
              <span>
                <i className="fa-solid fa-envelope"></i> info@efadh.com.sa
              </span>
              <span>
                {" "}
                <i className="fa-solid fa-phone"></i>920008831
              </span>
            </div>
            <div>
              <button className="btn">تسجيل</button>
              <button className="btn ">تسجيل الدخول</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default Navbar;
