import React from "react";
import "./Info.css";
import logo from "./img/efadh-logo.svg";
import glasses from "./img/glasses.svg";
import badge from "./img/badge.svg";
import mark from "./img/mark.svg";
import arrow from "./img/arrow.svg";

const Info = () => {
  return (
    <div className="info">
      <div className="container">
        <div className="know">
          <div className="overlay">
            <div className="title">
              <h2>تعرف على الشركة</h2>
            </div>
            <div className="pic">
              <img src={logo} alt="" />
            </div>
          </div>
        </div>
        <div className="details">
          <div className="details-text">
            <p>
              لاشك أن مشوار الألف ميل يبدأ بخطوة واحدة .
              <br />
              نحـن فـي شركـة إفـادة للمــوارد البشريـة بعـون الله تعالـي قد
              بـدأنـا هذا المـشـوار ،
              <br />
              ووضعـنا نصـب أعيننا رؤية مستقبلية واضحه وهي تقديم خدمة راقية في
              مجال توريد
              <br />
              العمالة بصورة قائمة على أحـدث النظـم التـي تتلاءم مع ثقافتنـا
              وتقالـيدنـا وتتماشي مـع مـا تتطلبـه المرحلـة القادمـة ورؤيـة 2030
            </p>
          </div>
          <div className="details-sec">
            <div className="cards row">
              <div className="card text-center col-2">
                <div className="card-img mt-3 mb-2 ">
                  <img src={glasses} alt="" />
                </div>
                <div className="card-title">روؤية</div>
              </div>
              <div className="card text-center col-2">
                <div className="card-img mt-3 mb-2 ">
                  <img src={mark} alt="" />
                </div>
                <div className="card-title">جودة</div>
              </div>
              <div className="card text-center col-2 ">
                <div className="card-img mt-3 mb-2">
                  <img src={badge} alt="" />
                </div>
                <div className="card-title">مهنية</div>
              </div>
              <div className="card text-center col-2 ">
                <div className="card-img mt-3 mb-2">
                  <img src={arrow} alt="" />
                </div>
                <div className="card-title">تطوير</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default Info;
