import React from "react";
import Navbar from "../Home/Navbar/lightNav/Navbar";
import { NavLink } from "react-router-dom";
import agent from "./SVGs/agent.svg";
import company from "./SVGs/company.svg";
import ind from "./SVGs/ind.svg";
import "./Services.css";
export const Services = () => {
  return (
    <>
      <Navbar />
      <div className="services">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <h2>خدماتنا في قطاع الأعمال</h2>
            </div>
          </div>
          <div className="row serv">
            <div className="col-md-3 col-xs-12 item">
              <NavLink to="/efadh-site/services/agent">
                <div className="head agent">
                  <img src={agent} alt="Agent Img" />
                </div>
                <div className="title">
                  <h3>تسجيل الدخول كـ وكيل</h3>
                </div>
              </NavLink>
            </div>
            <div className="col-md-3 col-xs-12col-xs-12 item">
              <NavLink to="">
                <div className="head company">
                  <img src={company} alt="Agent Img" />
                </div>
                <div className="title">
                  <h3>تسجيل الدخول كـ شركة</h3>
                </div>
              </NavLink>
            </div>
            <div className="col-md-3 col-xs-12 item">
              <NavLink to="">
                <div className="head ind">
                  <img src={ind} alt="Agent Img" />
                </div>
                <div className="title">
                  <h3>تسجيل الدخول كـ فرد</h3>
                </div>
              </NavLink>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
