import React from "react";
import one from "./img/1.svg";
import two from "./img/2.svg";
import three from "./img/3.svg";
import four from "./img/4.svg";
import "./Awards.css";
const Awards = () => {
  return (
    <div className="awards container">
      <div className="awards-bg">
        <div className="awards-overlay">
          <div className="awards-content">
            <div className="awards-latest">
              <h2>أبرز إنجازات الشركة</h2>
            </div>
            <div className="awards-cards-parent">
              <div className="awards-cards-text">
                <h2>احصائياتنا تتحدث</h2>
                <p>
                  هناك حقيقة مثبتة منذ زمن طويل وهي أن المحتوى المقروء لصفحة ما
                  سيلهي القارئ عن التركيز على الشكل الخارجي للنص أو شكل توضع
                  الفقرات في الصفحة التي يقرأها
                </p>
              </div>
              <div className="awards-cards">
                <div className="awards-card">
                  <div className="card-img">
                    <img src={one} alt="" />
                  </div>
                  <div className="card-text">
                    <h4>1475+</h4>
                    <p>زبائن يشعرون بالرضى</p>
                  </div>
                </div>
                <div className="awards-card">
                  <div className="card-img">
                    <img src={two} alt="" />
                  </div>
                  <div className="card-text">
                    <h4>15+</h4>
                    <p>سنوات خبرة</p>
                  </div>
                </div>
                <div className="awards-card">
                  <div className="card-img">
                    <img src={three} alt="" />
                  </div>
                  <div className="card-text">
                    <h4>32+</h4>
                    <p>أعضاء الفريق</p>
                  </div>
                </div>
                <div className="awards-card">
                  <div className="card-img">
                    <img src={four} alt="" />
                  </div>
                  <div className="card-text">
                    <h4>1224+</h4>
                    <p>الحالات المكتملة</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default Awards;
