import React from "react";
import "./ContactForm.css";
const ContactForm = () => {
  return (
    <div className="contact-form">
      <form>
        <div className="form-group">
          <label htmlFor="exampleInputEmail1">اسمك الشخصي</label>
          <input
            type="text"
            className="form-control"
            id="InputName"
            placeholder="ادخل اسمك الشخصي "
          />
        </div>
        <div className="form-group">
          <label htmlFor="exampleInputEmail1">الكنية</label>
          <input
            type="text"
            className="form-control"
            id="InputName"
            placeholder="ادخل الكنية"
          />
        </div>
        <div className="form-group">
          <label htmlFor="exampleInputEmail1">بريدك الالكتروني</label>
          <input
            type="email"
            className="form-control"
            id="exampleInputEmail1"
            ariaDescrbedby="emailHelp"
            placeholder="ادخل بريدك الالكتروني"
          />
          <small id="emailHelp" className="form-text text-muted">
            لن نقوم أبداً بمشاركة بريدك الالكتروني .
          </small>
        </div>
        <div className="form-group">
          <label htmlFor="exampleInputEmail1">رسالتك</label>
          <textarea
            className="form-control"
            id="exampleFormControlTextarea1"
            rows="3"
            placeholder="اكتب رسالتك هنا"
          ></textarea>
        </div>
        <button type="submit" className="btn btn-primary">
          ارسال
        </button>
      </form>
    </div>
  );
};
export default ContactForm;
