import React from "react";
import Navbar from "../../Home/Navbar/lightNav/Navbar";
import "./Agent.css";
export const Agent = () => {
  return (
    <>
      <Navbar />
      <div className="agent">
        <div className="agent-title">
          <h3>تسجيل الدخول كـ وكيل</h3>
        </div>
        <div className="agent-form">
          <form>
            <div className="form-group">
              <label htmlFor="exampleInputEmail1">البريد الإلكتروني</label>
              <input
                type="email"
                className="form-control"
                id="exampleInputEmail1"
                ariaDescribedby="emailHelp"
                placeholder="example@aa.com"
              />
            </div>
            <div className="form-group">
              <label htmlFor="exampleInputPassword1">كلمة المرور</label>
              <input
                type="password"
                className="form-control"
                id="exampleInputPassword1"
                placeholder="كلمة المرور"
              />
            </div>
            <div className="form-forget">
              <div>
                <label className="container">
                  {" "}
                  <input type="checkbox" />
                  <span className="mark"></span>
                  تذكرني
                </label>
              </div>
              <p>نسيت كلمة المرور ؟</p>
            </div>
            <button type="submit" className="btn btn-primary">
              تسجيل الدخول
            </button>
            <button className="newagent">
              ليس لديك حساب ؟ <span>تسجيل جديد</span>
            </button>
          </form>
        </div>
      </div>
    </>
  );
};
